na que mantienen con sus antepasados y su entorno. As, la cosmogona da sentido no solo a la vida, sino a las formas de cuidado comunitario, pues orienta las normas y creencias sobre el cuerpo, la enfermedad y la relacin con lo espiritual y natural.")])]),_c('div',{staticClass:"col-12 mb-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-24.png"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row mb-0",attrs:{"numero":"2","titulo":"Cada comunidad, una visin propia"}},[_c('div',{staticClass:"col-12"},[_c('p',[_vm._v("La cosmogona no es exclusiva de los pueblos indgenas. Cualquier grupourbano, rural o de otra procedenciatiene sus propias maneras de comprender el mundo. Por eso, las prcticas y conocimientos ligados a la salud deben surgir del respeto y el dilogo entre todas las personas, sin imponer una nica forma de ver la vida. As se fomenta el respeto mutuo, la inclusin y la equidad, haciendo que todos se sientan parte de las decisiones que afectan su bienestar. As se evitan imposiciones, se favorece el dilogo abierto, la inclusin, el consentimiento y la equidad. ")]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-25.png"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row mb-0",attrs:{"numero":"3","titulo":"Diversidad de prcticas y saberes"}},[_c('div',{staticClass:"col-12"},[_c('p',[_vm._v("En Colombia, la diversidad es enorme. Los afrocolombianos, los Arhuacos y los pueblos andinos cuentan con sanadores tradicionales, celebraciones, rituales agrarios y conocimientos que han pasado de generacin en generacin. Prcticas como el buen vivir (sumak kawsay) muestran cmo la salud surge de armonizar relaciones humanas, naturaleza y costumbres ancestrales. Aunque existen similitudes, cada grupo establece sus propias normas sobre gnero, comida, la vida, la muerte y la forma de ver el territorio (Lpez, et al, 2018)")]),_c('p',[_vm._v("Armonizar estas dimensiones permite adaptar la prevencin, la promocin y la atencin, garantizando el bienestar integral segn las realidades de cada pueblo o comunidad. Pese a las similitudes, se pude decir que cada uno define normas respecto al gnero, la edad, la alimentacin, la muerte, la espiritualidad y el uso del territorio acorde a ellos. Las prcticas de cuidado y sanacin no solo buscan curar sntomas fsicos, sino restablecer el equilibrio espiritual, econmico, familiar, ambiental y social, recurriendo a disimiles rituales tales como plantas medicinales, curanderos, limpias, baos, agradecimientos y mediaciones con los ancestros. ")]),_c('p',[_vm._v("Comprender y respetar la cosmogona permite disear intervenciones de salud pblica pertinentes, incorporar promotores culturales, fortalecer el trabajo participativo y dialogar con las prcticas de autocuidado y sanacin colectiva, promoviendo una perspectiva intercultural y el reconocimiento de la autonoma de los pueblos.")])]),_c('div',{staticClass:"col-12 mb-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-26.png"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row mb-0",attrs:{"numero":"4","titulo":"Dilogo entre medicina ancestral y occidental"}},[_c('div',{staticClass:"col-12"},[_c('p',[_vm._v("Las personas sabias, como chamanes, curanderos y parteras, tienen un papel central en la salud comunitaria. Saben cmo usar plantas, smbolos y rituales; adems, muchas veces median entre la medicina tradicional y la occidental. Por eso, es importante que los sistemas de salud oficial reconozcan estos saberes y se adapten respetuosamente a cada comunidad. La Organizacin Panamericana de la Salud, recomienda flexibilidad, dilogo y empata para que los protocolos mdicos sean efectivos y culturalmente pertinentes porque permite interactuar entre la visin occidental, la medicina tradicional y salud comunitaria para lograr un bienestar integral, sostenible e inclusivo (Organizacin Panamericana de la Salud, 2025).")]),_c('p',[_vm._v("El enfoque de interculturalidad en salud es un eje transversal que busca potenciar el acceso y la eficacia de las intervenciones, valorando la diversidad y promoviendo la equidad. Para lograrlo, es crucial establecer espacios de comunicacin y negociacin entre los profesionales de la salud, lderes comunitarios y sabedores ancestrales. Este enfoque implica el reconocimiento de las prcticas, rituales y conocimientos locales como elementos vlidos y complementarios a los procesos de atencin biomdica, eliminando as juicios etnocentristas.")]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-27.png"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row mb-0",attrs:{"numero":"5","titulo":"Competencia cultural y enfoque intercultural"}},[_c('div',{staticClass:"col-12"},[_c('p',[_vm._v("Atender la salud no solo es tratar enfermedades: exige reconocer la diversidad y la cultura de cada persona. Por eso, quienes desarrollan acciones en salud deben aprender y practicar competencia culturaluna capacidad que se construye con conciencia, conocimientos y habilidades para entender y respetar creencias y comportamientos diferentes. Ignorar la cultura es ignorar a la persona misma y eso puede dificultar cualquier proceso de cuidado o sanacin.")])]),_c('div',{staticClass:"col-12 mb-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-28.png"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row mb-0",attrs:{"numero":"6","titulo":"Hacia una salud ms inclusiva"}},[_c('div',{staticClass:"col-12"},[_c('p',[_vm._v("El enfoque intercultural busca mejorar la salud y el bienestar sumando conocimientos mdicos y saberes ancestrales, eliminando prejuicios y sumando voces, desde las parteras tradicionales hasta los lderes espirituales y comunitarios. De este modo, la prevencin, promocin y la atencin mdica se vuelven ms pertinentes y equitativas. Un ejemplo de esto es el parto intercultural, donde se respeta tanto lo mdico como lo tradicional o el us