},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"col-lg-12 col-12"},[_c('p',[_vm._v("El anlisis participativo es una herramienta fundamental en el proceso de la Vigilancia Basada en Comunidad. Este proceso es realizado por los miembros de la comunidad, agentes comunitarios y personal de salud e instituciones que son parte de un territorio, con el fin de cooperar con la identificacin, anlisis y movilizacin social, frente a las situaciones de inters en salud pblica que se presentan en estos (Geilfus, 2002). Para esto, la comunidad se vale de las Redes de Vigilancia Epidemiolgica Basada en Comunidad (REVCOM), siendo esta la encargada de emitir las seales al Sistema de Alerta Temprana (SAT), para iniciar la gestin del riesgo colectivo e individual de estas. Con la participacin activa de la comunidad se favorece la proteccin de la salud, as como, influir en la toma de decisiones y ejercer control social de las acciones que se desarrollan en los territorios. Estas acciones pueden realizarse en espacios como COVECOM, comits locales, mesas de trabajo, en los que se comparte la presentacin de las situaciones de salud, las actividades realizadas con la comunidad, as como la respuesta institucional a estas. ")])])])
