    p.mb-4 Las etapas del ciclo de vida son los grandes periodos de desarrollo que atraviesa una persona desde la concepcin hasta la muerte y constituyen el eje central para los estudios, De acuerdo con la Resolucin 3202 de 2016 en Colombia, las etapas del ciclo de vida establecidas son las siguientes: 

