            h3.mb-0 Enfoque y conceptos claves

    p Las etapas del ciclo de vida son los periodos cronolgicos en el desarrollo humano (primera infancia, infancia, adolescencia, juventud, adultez, vejez), definidos por rangos de edad, caractersticas biolgicas, psicolgicas y sociales.
    p.mb-4 El ciclo de vida implica que los riesgos y oportunidades para la salud varan segn el estadio y se reconoce que las exposiciones tempranas (como la nutricin prenatal y la infancia) pueden tener efectos duraderos. Esto se estudia con el fin de identificar la incidencia y prevalencia de enfermedades y eventos de salud en cada etapa, reconociendo ventanas de oportunidad y momentos crticos para intervenciones pblicas.

