        p.mb-0 Las etapas del ciclo de vida son as herramienta fundamental para organizar el estudio de poblaciones y planificar acciones de salud pblica eficaces y adaptadas, asimismo, estas etapas permiten organizar la atencin integral en salud reconociendo las caractersticas y necesidades especficas de cada momento vital a lo largo del curso de vida (Ministerio de Salud y Proteccin Social, 2016).

