          p Por su parte, las RIAS (Rutas Integrales de Atencin en Salud) constituyen paquetes organizados de acciones para la atencin, prevencin y promocin en salud, diseadas especficamente para las necesidades particulares de cada etapa del ciclo de vida, por esto, cada momento de los mencionados tiene una o varias RIAS asociadas que determinan los protocolos y servicios especficos para atender las particularidades en salud de ese grupo de edad (Ministerio de Salud y Proteccin Social, 2016).
