    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.2. Condiciones socioambientales

    p.mb-4 Las condiciones socioambientales son el conjunto de factores sociales y ambientales que interactan para influir en la salud de los individuos y comunidades (Departamento Nacional de Planeacin, 2008). Se entienden como las circunstancias derivadas de la interaccin entre personas y su entorno fsico (aire, agua, suelo), qumico, biolgico y social (hbitos, relaciones, organizacin comunitaria). Estas condiciones incluyen aspectos como calidad del aire, acceso a agua potable, vivienda adecuada, saneamiento, exposicin a contaminantes, ruido ambiental, condiciones de trabajo y acceso a espacios verdes.

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-4(style="background-color:#FFC025")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema1/img-16.png')})`}" data-aos="fade-right"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-left")
        p.mb-0 En salud pblica las condiciones socioambientales se valoran como determinantes porque influencian la aparicin y frecuencia de enfermedades, as como la calidad de vida de la poblacin. La contaminacin atmosfrica, la segregacin residencial, el acceso desigual a servicios y el cambio climtico son factores socioambientales que pueden generar disparidades en salud, especialmente en grupos vulnerables como nios y adultos mayores (Watson Lewis et al., s.f.).

    p.mb-4.text-center Para complementar lo anterior, a continuacin, se relacionan algunas condiciones ambientales y la consecuencia presentada por cada una: 

