          p.mb-0 Exposicin a hacinamiento y sin ventilacin, que favorece la transmisin de infecciones.
        .tarjeta.tarjeta--BGC02.p-4
          .row.justify-content-center.mb-4
            .col-lg-5.col-sm-4.col-6
              img(src='@/assets/curso/temas/tema1/img-20.svg' alt='AvatarTop')
          h4.text-center Ruido ambiental elevado
          p.mb-0 Barrios cercanos a las autopistas o zonas comerciales, implicando insomnio y estrs crnico.
