          h4.text-center Trabajo en condiciones insalubres 
          p.mb-0 Exposicin ocupacional a sustancias qumicas peligrosas (plomo, solventes, pesticidas), vinculados a enfermedades ocupacionales.
        .tarjeta.tarjeta--BGC02.p-4
          .row.justify-content-center.mb-4
