          p.mb-0 Las condiciones socioambientales reflejan la manera en que la organizacin social y el entorno fsico pueden proteger o perjudicar la salud; adems, son objeto central de polticas pblicas orientadas a la equidad, la prevencin y la promocin de la vida saludable (Departamento Nacional de Planeacin, 2008).

