    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.3. Aspectos culturales y cosmogona

    p La cosmogona y los aspectos culturales de una comunidad son la base que da sentido y gua a la vida de las personas, su salud y la manera en que cuidan de s mismas y de quienes las rodean. Cada grupo humano, segn su historia, etnia y entorno, cuenta con saberes, valores, costumbres y relatos propios sobre cmo surgi la vida y qu significa estar bien o enfermarse. Por eso, todo lo que se pensaba sobre los antepasados acerca de la salud, sigue influyendo hasta hoy en cmo se sanan, previenen enfermedades y buscan sentirse bien en compaa de las familias y los vecinos. 
    p.mb-4 Para entender esta temtica, es preciso revisar los siguientes ejes de comprensin intercultural: 

    .row.justify-content-center.mb-5
      .col-lg-10.col-12
        LineaTiempoD.color-acento-botones.especial(data-aos="zoom-in")
          .row.mb-0(numero="1" titulo="Cosmogona: el origen, la vida y la salud")(data-aos="fade-left")
            .col-12
              p En las comunidades indgenas de la Amazona, por ejemplo, la salud depende del equilibrio entre la persona, la comunidad y la naturaleza. Los rituales, la sanacin y las plantas medicinales tienen un papel fundamental. Para los Wayu de Colombia, la salud est profundamente conectada con la armona que mantienen con sus antepasados y su entorno. As, la cosmogona da sentido no solo a la vida, sino a las formas de cuidado comunitario, pues orienta las normas y creencias sobre el cuerpo, la enfermedad y la relacin con lo espiritual y natural.
            .col-12.mb-0
              figure
                img(src='@/assets/curso/temas/tema1/img-24.png', alt='Imagen decorativa')
          .row.mb-0(numero="2" titulo="Cada comunidad, una visin propia")
            .col-12
              p La cosmogona no es exclusiva de los pueblos indgenas. Cualquier grupourbano, rural o de otra procedenciatiene sus propias maneras de comprender el mundo. Por eso, las prcticas y conocimientos ligados a la salud deben surgir del respeto y el dilogo entre todas las personas, sin imponer una nica forma de ver la vida. As se fomenta el respeto mutuo, la inclusin y la equidad, haciendo que todos se sientan parte de las decisiones que afectan su bienestar. As se evitan imposiciones, se favorece el dilogo abierto, la inclusin, el consentimiento y la equidad.  
              figure
                img(src='@/assets/curso/temas/tema1/img-25.png', alt='Imagen decorativa')
          .row.mb-0(numero="3" titulo="Diversidad de prcticas y saberes")
            .col-12
              p En Colombia, la diversidad es enorme. Los afrocolombianos, los Arhuacos y los pueblos andinos cuentan con sanadores tradicionales, celebraciones, rituales agrarios y conocimientos que han pasado de generacin en generacin. Prcticas como el buen vivir (sumak kawsay) muestran cmo la salud surge de armonizar relaciones humanas, naturaleza y costumbres ancestrales. Aunque existen similitudes, cada grupo establece sus propias normas sobre gnero, comida, la vida, la muerte y la forma de ver el territorio (Lpez, et al, 2018)
              p Armonizar estas dimensiones permite adaptar la prevencin, la promocin y la atencin, garantizando el bienestar integral segn las realidades de cada pueblo o comunidad. Pese a las similitudes, se pude decir que cada uno define normas respecto al gnero, la edad, la alimentacin, la muerte, la espiritualidad y el uso del territorio acorde a ellos. Las prcticas de cuidado y sanacin no solo buscan curar sntomas fsicos, sino restablecer el equilibrio espiritual, econmico, familiar, ambiental y social, recurriendo a disimiles rituales tales como plantas medicinales, curanderos, limpias, baos, agradecimientos y mediaciones con los ancestros. 
              p Comprender y respetar la cosmogona permite disear intervenciones de salud pblica pertinentes, incorporar promotores culturales, fortalecer el trabajo participativo y dialogar con las prcticas de autocuidado y sanacin colectiva, promoviendo una perspectiva intercultural y el reconocimiento de la autonoma de los pueblos.
            .col-12.mb-0
              figure
                img(src='@/assets/curso/temas/tema1/img-26.png', alt='Imagen decorativa')
          .row.mb-0(numero="4" titulo="Dilogo entre medicina ancestral y occidental")
            .col-12
              p Las personas sabias, como chamanes, curanderos y parteras, tienen un papel central en la salud comunitaria. Saben cmo usar plantas, smbolos y rituales; adems, muchas veces median entre la medicina tradicional y la occidental. Por eso, es importante que los sistemas de salud oficial reconozcan estos saberes y se adapten respetuosamente a cada comunidad. La Organizacin Panamericana de la Salud, recomienda flexibilidad, dilogo y empata para que los protocolos mdicos sean efectivos y culturalmente pertinentes porque permite interactuar entre la visin occidental, la medicina tradicional y salud comunitaria para lograr un bienestar integral, sostenible e inclusivo (Organizacin Panamericana de la Salud, 2025).
              p El enfoque de interculturalidad en salud es un eje transversal que busca potenciar el acceso y la eficacia de las intervenciones, valorando la diversidad y promoviendo la equidad. Para lograrlo, es crucial establecer espacios de comunicacin y negociacin entre los profesionales de la salud, lderes comunitarios y sabedores ancestrales. Este enfoque implica el reconocimiento de las prcticas, rituales y conocimientos locales como elementos vlidos y complementarios a los procesos de atencin biomdica, eliminando as juicios etnocentristas.
              figure
                img(src='@/assets/curso/temas/tema1/img-27.png', alt='Imagen decorativa')
          .row.mb-0(numero="5" titulo="Competencia cultural y enfoque intercultural")
            .col-12
              p Atender la salud no solo es tratar enfermedades: exige reconocer la diversidad y la cultura de cada persona. Por eso, quienes desarrollan acciones en salud deben aprender y practicar competencia culturaluna capacidad que se construye con conciencia, conocimientos y habilidades para entender y respetar creencias y comportamientos diferentes. Ignorar la cultura es ignorar a la persona misma y eso puede dificultar cualquier proceso de cuidado o sanacin.
            .col-12.mb-0
              figure
                img(src='@/assets/curso/temas/tema1/img-28.png', alt='Imagen decorativa')
          .row.mb-0(numero="6" titulo="Hacia una salud ms inclusiva")
            .col-12
              p El enfoque intercultural busca mejorar la salud y el bienestar sumando conocimientos mdicos y saberes ancestrales, eliminando prejuicios y sumando voces, desde las parteras tradicionales hasta los lderes espirituales y comunitarios. De este modo, la prevencin, promocin y la atencin mdica se vuelven ms pertinentes y equitativas. Un ejemplo de esto es el parto intercultural, donde se respeta tanto lo mdico como lo tradicional o el uso de plantas medicinales avaladas por las costumbres y coordinadas con los profesionales de la salud y las parteras o quienes realizan acciones para la atencin del parto desde la comunidad.
              figure
                img(src='@/assets/curso/temas/tema1/img-29.png', alt='Imagen decorativa')

    .row.justify-content-center
      .col-xl-10
        .cajon.cajon.color-primario.row.align-items-center.m-0.style___box_1.p-4(data-aos="zoom-in").espacio___movil_1
