          .col-xl
            p.mb-0 En conclusin, comprender y respetar la cosmogona y los saberes tradicionales, permite construir una salud pblica capaz de responder mejor a las necesidades de cada comunidad, incorporando la diversidad, el dilogo y el trabajo conjunto hacia el bienestar integral de todas y todos.

