      h2 1.4. Identificacin de lderes en la comunidad

    p La identificacin de lderes, las formas de organizacin y las caractersticas de las organizaciones de base comunitaria, junto con los espacios de integracin, son aspectos esenciales para el desarrollo comunitario y la participacin social. De acuerdo con el Secretara de Integracin Social (2016), se tienen diversas formas de identificar liderazgos: se basa en analizar las concepciones y los mtodos que un lder utiliza. 
    p.mb-4 Los estilos de liderazgo se clasifican en #[b funcin de su nivel de control y participacin.] Por un lado, se encuentran los estilos ms tradicionales, encontrando:    

