                  h4 Liderazgo transformacional
                  p Se enfoca en inspirar a las personas a seguir una visin compartida que se alinea con sus aspiraciones, con el objetivo de lograr un cambio real y significativo.
    
    .row.justify-content-center.mb-4(data-aos="flip-up")
