          .contenido-tarjeta.p-4
            p.mb-0(data-aos="fade-down") En esencia, para identificar un estilo de liderazgo, es necesario observar si el lder se gua por la supervisin, el empoderamiento, la provisin de una visin o el uso del poder. 

    .row.justify-content-center.align-items-center.mb-4
