      .col-9
        p.mb-0(data-aos="fade-right") Adems de los beneficios internos, el desarrollo de liderazgo incrementa el alcance y la penetracin de las intervenciones. Al conectar a ms personas con los programas, se logra un mayor impacto a nivel de la poblacin, ya que se aprovechan las redes sociales existentes. Esto tambin ayuda a mantener las tasas de cambio comunitario a largo plazo, ya que el nuevo liderazgo con fuertes lazos sociales con la comunidad puede sostener los esfuerzos en el tiempo.

    .row.justify-content-center.mb-4
      .col-lg-5.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4
