      .col-lg-7.order-lg-1.order-2.mb-lg-0.mb-4
        p(style="color:#000") Finalmente, la implementacin de mtodos democrticos y consensuados para la toma de decisiones, que son una parte inherente del desarrollo de liderazgo, contribuye a aumentar la satisfaccin de los miembros y a mejorar el funcionamiento general de cualquier iniciativa 
        p(style="color:#000") De esta forma, la identificacin se realiza al observar quin motiva, moviliza, representa y promueve el bienestar colectivo, algunos ejemplos son:
