    p.mb-4 Las organizaciones de base comunitaria se caracterizan por su naturaleza profundamente local y su enfoque en las necesidades y problemas especficos de un rea geogrfica, como una vereda, un barrio o una ciudad. Estas organizaciones operan bajo un modelo de liderazgo comunitario, donde son los propios liderazgos locales quienes toman las riendas de la organizacin y dirigen las actividades.

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-4(style="background-color:#FFF3D3")
