        p.mb-0 Este enfoque fomenta la participacin, el empoderamiento y la movilizacin de los miembros de la comunidad, animndolos a involucrarse directamente en la bsqueda de soluciones y en la implementacin de proyectos que los beneficien. Adems de su base local, estas iniciativas son predominantemente sin fines de lucro, lo que significa que todos sus recursos y esfuerzos se reinvierten directamente en la comunidad que sirven. Su propsito principal es mejorar el bienestar social y el funcionamiento general del grupo.

    .row.justify-content-center(data-aos="fade-right")
      .col-lg-10.col-12
        p Por ello, su trabajo se orienta a desarrollar soluciones que promuevan la salud, el progreso y la calidad de vida de sus miembros. Todo esto garantiza que las iniciativas comunitarias se mantengan fieles a su misin de servir y empoderar a las comunidades en los territorios (Secretara de Integracin Social, 2016). 
