            h3.mb-0 Espacios de integracin o encuentros comunitarios 

    p.mb-4 La creacin de lugares apropiados para la interaccin se fundamenta en cuatro caractersticas esenciales que fomentan la participacin y la cohesin social. Estos espacios deben ofrecer a las personas un motivo para ir y otro para quedarse. Adems, es crucial que los usuarios se sientan seguros y cmodos y que el lugar sea acogedor y accesible para todos (Secretara de Integracin Social, 2016). 

    .row.justify-content-center.mb-0
      .col-lg-10.col-12
        .cajon.cajon.C01.px-4.py-4.mb-4
          .row.justify-content-center
            .col-lg-2.col-4.mb-lg-0.mb-4
              figure(data-aos="fade-down")
                img(src='@/assets/curso/temas/tema1/img-71.svg', alt='Imagen decorativa')
            .col-lg-10.col-12
              p.mb-0(data-aos="fade-down") Son mbitos donde se renen los miembros para fortalecer vnculos, discutir problemas, acordar acciones y celebrar logros. Particularmente en el contexto colombiano existen espacios como asambleas y reuniones peridicas de asociaciones vecinales; ferias, mercados campesinos, ferias artesanales o eventos deportivos; centros comunitarios, bibliotecas, grupos de lectura o culturales, comits, parques urbanos y espacios de aprendizaje compartido; talleres participativos, jornadas de limpieza, noches de cine al aire libre y fiestas tradicionales. Estos elementos son fundamentales para el desarrollo sostenible y la cohesin social en cualquier comunidad.
        p Puntualmente, para el caso de la estrategia de vigilancia basada en comunidad este espacio corresponde al Comit Epidemiolgico de Vigilancia Comunitaria (COVECOM) u otros en los que se ponen en discusin los temas que afecta la salud de la comunidad.

    Separador

    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.5. Soluciones a los problemas en salud

    p.mb-4 Las soluciones a los problemas de salud en una comunidad son intervenciones estructuradas y acciones colectivas diseadas para mejorar el bienestar de la poblacin. Estas iniciativas abordan directamente los factores que causan enfermedad y malestar, promoviendo una participacin activa de los miembros de la comunidad. Son esfuerzos coordinados, a menudo en colaboracin con instituciones y equipos multidisciplinarios, que buscan resolver las principales necesidades sanitarias de una poblacin, reducir las inequidades y mejorar su calidad de vida. Un aspecto fundamental de estas soluciones es que consideran la realidad local, los factores sociales, ambientales, econmicos y la cultura en la toma de decisiones, asegurando as una pertinencia y un impacto mayores.

    .row.justify-content-center.mb-4(data-aos="fade-left")
      .col-lg-9.order-2
        .tarjeta.tarjeta--BGC01.p-4.mb-4
          p.mb-0 En el mbito de la salud pblica, estas soluciones se manifiestan a travs de una serie de estrategias integrales. Se favorecen los modelos de trabajo que abarcan un abordaje multisectorial, lo que significa que la salud no se ve como una responsabilidad exclusiva del sector mdico, sino que involucra a la educacin, el urbanismo, el medio ambiente y otros campos. La participacin comunitaria y el trabajo en red, a travs de alianzas locales e intersectoriales, son cruciales. Este enfoque colaborativo garantiza que la planificacin, ejecucin y evaluacin de las acciones respondan de manera efectiva a las necesidades nicas de cada territorio, promoviendo la prevencin, el acceso equitativo a servicios y una mejora sostenible de la calidad de vida. 
        p Una de las soluciones ms comunes y efectivas en salud pblica son las #[b campaas de vacunacin.] Su objetivo principal es reducir la incidencia de enfermedades prevenibles por inmunizacin, protegiendo a individuos y a la comunidad en general. Complementariamente, se implementan #[b programas de educacin para la salud], los cuales incluyen talleres sobre nutricin, salud sexual, prevencin de adicciones y otras enfermedades en distintos entornos, como instituciones educativas para nios y centros de atencin para adultos mayores.
      .col-lg-3.col-md-6.col-8.order-1.mb-lg-0.mb-4
        figure
          img(src='@/assets/curso/temas/tema1/img-47.svg', alt='Imagen decorativa')

    .row.justify-content-center.mb-4
      .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
        p(style="color:#000") El trabajo colaborativo va ms all de los programas educativos. Se organizan #[b mesas de trabajo] que renen a lderes institucionales y comunitarios, desde el orden nacional hasta el municipal. Estos espacios de concertacin son fundamentales para identificar situaciones de inters en salud y para coordinar respuestas efectivas. Adems, se desarrollan #[b proyectos de infraestructura] que impactan directamente en la salud, como la creacin de "caminos escolares seguros" para reducir accidentes, mejorar la infraestructura urbana y fomentar hbitos saludables en la poblacin escolar y joven. 
        .cajon.cajon.color-primario.p-4
          p.mb-0 La promocin de estilos de vida saludables es otra solucin prioritaria. Esto abarca un amplio espectro de acciones, que van desde fomentar la #[b alimentacin y nutricin sana] y promover el #[b ejercicio fsico] hasta trabajar en la #[b disminucin de conductas no saludables,] como el consumo de cigarrillo, alcohol y sustancias psicoactivas. Estas iniciativas se centran en el empoderamiento individual y colectivo para tomar decisiones que mejoren el bienestar a largo plazo. 
      .col-lg-5.col-6.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema1/img-48.png' alt="Imagen decorativa")

    p Por ltimo, es indispensable garantizar el #[b acceso a servicios mdicos de forma oportuna y eficaz.] Esto requiere una infraestructura adecuada, como centros de salud bien equipados y con personal idneo. En zonas remotas de difcil acceso, las #[b brigadas de salud y la telemedicina] se han consolidado como herramientas esenciales para brindar atencin. Adicionalmente, las #[b intervenciones ambientales] son cruciales, con mejoras en los sistemas de agua potable, saneamiento bsico, gestin de residuos slidos y control de vectores. Todas estas soluciones tienen un mayor impacto cuando no son exclusivas de unos pocos actores, sino que integran la voz y la participacin de la comunidad en todas sus fases, desde el diagnstico hasta la evaluacin de los resultados.

    Separador

    #t_1_6.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.6. Diagnstico participativo

    p.mb-4 El diagnstico participativo es una metodologa comunitaria que involucra activamente a los miembros de la comunidad para analizar su realidad, identificar necesidades, recursos y oportunidades, as como decidir colectivamente sobre los problemas prioritarios y sus posibles soluciones. Se trata de una construccin colectiva que promueve la reflexin, la apropiacin y el desarrollo de capacidades dentro de la comunidad. (Gobierno Federal, s.f.; Gom, 2007).

    .row.justify-content-center.mb-4(data-aos="fade-right")
      .col-1.d-none.d-lg-block
        figure
          img(src='@/assets/curso/temas/tema1/img-86.svg', alt='Imagen decorativa')
      .col-9
        p Este proceso se aplica con frecuencia en proyectos comunitarios para garantizar una planeacin social que oriente intervenciones en sectores como la salud, la educacin, la gestin de recursos o cualquier mbito que requiera comprender la realidad desde la perspectiva de quienes la viven.

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-4(style="background-color:#FFC025")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema1/img-49.png')})`}" data-aos="fade-right"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-left") 
        p.mb-0 Esta metodologa permite a la comunidad ser la protagonista en la identificacin, comprensin y priorizacin de sus propios problemas y fortalezas. A diferencia de los diagnsticos tradicionales que son realizados por expertos externos, este enfoque busca que la comunidad se apropie del conocimiento sobre su realidad, permitindole no solo reconocer sus desafos, sino tambin sus propios recursos y capacidades para resolverlos. El proceso fortalece el capital social y genera propuestas de solucin que son legtimas y viables porque surgen del consenso y la experiencia de quienes viven la situacin. (Gom, 2007) 
    
    p.mb-4 El diagnstico participativo genera una visin compartida de la situacin comunitaria, fortalece la organizacin local y legitima las decisiones colectivas. Basado en Gom (2007), sus principales resultados son: 

