    #t_1_7.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.7. Herramientas de diagnstico participativo

    p Existen mltiples herramientas y tcnicas de diagnstico participativo diseadas para involucrar activamente a las comunidades en el anlisis de sus propias realidades. Estas metodologas se caracterizan por ser flexibles y visuales, lo que facilita la comprensin y el dilogo entre personas con distintos niveles de alfabetizacin o formacin acadmica. Su propsito principal es generar un saber colectivo que empodere a la comunidad. 
    p.mb-4 De acuerdo a lo anterior, a continuacin, se presentan algunas de las herramientas ms representativas y eficaces, con su debida explicacin: 

    .tarjeta--BG04.p-4.px-md-5.mb-4
      .tarjeta.tarjeta--FBG01.p-4.my-4(data-aos="fade-left")
        PasosB.color-acento-botones
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Mapeo comunitario
              p.mb-0(style="color:#000 !important") Es una estrategia enriquecedora, su elaboracin est inmersa dentro de un proceso de organizacin y articulacin comunitaria; adems, consiste en que los propios habitantes dibujen un mapa de su territorio. As, se ponen de manifiesto las dinmicas espaciales, las desigualdades y los puntos crticos, lo que permite a los participantes reflexionar sobre el impacto de su entorno en el bienestar colectivo. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4  
              figure
                img(src='@/assets/curso/temas/tema1/img-58.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") rbol de problemas y soluciones
              p.mb-0(style="color:#000 !important") Resulta til para profundizar en las causas y consecuencias de una problemtica concreta. El tronco del rbol representa el problema central, las races simbolizan las causas subyacentes y las ramas los efectos o repercusiones. Una vez terminado el rbol de problemas, se construye el rbol de soluciones, donde las causas se transforman en acciones concretas y los efectos negativos en resultados positivos. Esta metodologa promueve el pensamiento crtico y estratgico. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-59.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Matrices de priorizacin
              p.mb-0(style="color:#000 !important") Permiten a la comunidad valorar y clasificar sus problemas segn criterios consensuados como la importancia, urgencia o viabilidad de solucin. Los participantes votan o asignan puntajes a cada asunto, favoreciendo la toma de decisiones colectiva y legitimando las prioridades de accin. De este modo, los proyectos se enfocan en las necesidades ms urgentes del grupo. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-60.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Grupos focales
              p.mb-0(style="color:#000 !important") Son espacios de dilogo guiado donde se rene a un pequeo grupo de personas con caractersticas similares (por ejemplo, jvenes, mujeres, lderes comunitarios) para profundizar sobre un tema especfico. Un facilitador modera la discusin y crea un ambiente de confianza, permitiendo que los participantes compartan sus experiencias, percepciones y opiniones, lo que enriquece la informacin cualitativa obtenida.
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-61.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Historias de vida y los relatos comunitarios
              p.mb-0(style="color:#000 !important") Emplean tcnicas narrativas que recogen experiencias individuales y la memoria colectiva de la comunidad. Documentar las trayectorias de vida o narrar la historia de algn acontecimiento relevante permite conocer la evolucin de los problemas y las soluciones a lo largo del tiempo. Estas historias fortalecen la identidad, la resiliencia y sirven de aprendizaje para las nuevas generaciones. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-62.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") El Diagrama de Venn
              p.mb-0(style="color:#000 !important") Es una herramienta ideal para analizar las relaciones entre actores sociales e instituciones presentes en el territorio. Los participantes dibujan crculos que representan grupos, organizaciones e instituciones; el tamao y la superposicin de los crculos indica la importancia y el nivel de interaccin entre ellos. As, se visualizan las redes de poder y colaboracin, as como los aliados clave y posibles vacos en la articulacin social. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-63.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Caminatas de observacin
              p.mb-0(style="color:#000 !important") Consisten en recorridos guiados por miembros de la comunidad a travs de su espacio. Durante la caminata, junto con el facilitador, observan, documentan y dialogan sobre los recursos, problemas y oportunidades que se encuentran. Esta tcnica brinda un conocimiento directo y fomenta la reflexin colectiva sobre los aspectos fsicos y sociales del entorno. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-64.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Calendario estacional
              p.mb-0(style="color:#000 !important") Es una herramienta que ayuda al grupo a visualizar los cambios en actividades, eventos y problemas a lo largo del ao. Se seala en el calendario cundo ocurren las pocas de siembra y cosecha, las enfermedades ms frecuentes, las fiestas tradicionales o los periodos de mayor escasez de alimentos. Esta informacin resulta esencial para planificar intervenciones ajustadas a las particularidades culturales y temporales de la comunidad. 
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-65.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Lnea de tiempo histrica
              p.mb-0(style="color:#000 !important") Permite reconstruir cronolgicamente los eventos ms significativos de la comunidad. Se identifican hitos como la llegada de servicios pblicos, desastres naturales o el inicio de proyectos colectivos. Este ejercicio no solo ayuda a comprender la evolucin de los problemas, sino tambin a reconocer la resiliencia y la capacidad del grupo para superar desafos a lo largo de los aos.
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-66.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Lluvias o tormenta de ideas
              p.mb-0(style="color:#000 !important") Consiste en que los participantes sugieren propuestas y soluciones a partir de una problemtica comn de personas sentadas en crculo alrededor de una hoja grande, escribiendo o dibujando propuestas en papel, colocando notas adhesivas, pequeas ilustraciones y grficos que expresan sus aportes de forma colectiva y visual (Geilfus, 2002).
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-67.png', alt='Imagen decorativa')
          .row
            .col-lg-6.order-2.order-lg-1
              h4(style="color:#000 !important") Encuestas participativas
              p.mb-0(style="color:#000 !important") En ellas los participantes responden preguntas abiertas sobre su entorno y necesidades, permitiendo ajustar planes segn el contexto local, por medio de una serie de formularios o cuestionarios abiertos con conos de personas rellenando respuestas en grupo o de manera individual, representando cmo se recogen las opiniones en papel o digitalmente (Geilfus, 2002).
            .col-lg-6.col-10.order-1.order-lg-2.mb-4
              figure
                img(src='@/assets/curso/temas/tema1/img-68.png', alt='Imagen decorativa')

    p.mb-4 Conociendo ya las principales herramientas de diagnstico participativo, es importante ahondar un poco y ejemplificar las siguientes:

    .row.mb-4.ms-0
      .col-auto.bg-c1(data-aos="fade-left")
