        .row.bg-color-5.justify-content-center.align-items-center.p-4
          .col-lg-10
            figure.desktop
              img(src='@/assets/curso/temas/tema1/figura-2-1v.svg', alt='Descripcin: La ilustracin presenta a una persona pensativa rodeada de burbujas de dilogo con preguntas relacionadas con las acciones de personas con discapacidad. La escena se compone de una figura central, una persona con cabello rojizo y vestimenta verde, situada en el centro de la imagen. Alrededor de ella, se dispersan tres burbujas de dilogo que contienen preguntas relacionadas con la manera en cmo se hace referencia a las personas con discapacidad. La composicin est equilibrada, guiando la vista del espectador a travs de las preguntas y hacia la persona central, quien parece estar reflexionando sobre ellas, sealada por los signos de interrogacin sobre su cabeza. La disposicin de las burbujas de dilogo y su tamao varan, creando un ritmo visual y enfatizando la importancia de cada cuestin. El sujeto principal es una persona joven, con cabello rojizo largo y una expresin de preocupacin, con las manos entrelazadas bajo la barbilla. La vestimenta de la persona consiste en una blusa verde oscura. Los signos de interrogacin sobre su cabeza indican incertidumbre o reflexin. El estilo de la imagen es simple y directo, con lneas limpias y colores planos, lo que sugiere una ilustracin didctica o explicativa. La tcnica empleada parece ser digital, con contornos definidos y un diseo grfico. El uso de burbujas de dilogo es caracterstico de las ilustraciones que buscan comunicar informacin de manera clara y concisa. La paleta de colores es limitada, con tonos suaves y agradables, lo que contribuye a la legibilidad y facilita la comprensin de la informacin. El fondo es blanco, lo que centra la atencin en la persona y las preguntas. La iluminacin es uniforme, sin sombras ni efectos especiales, lo que enfatiza la claridad y la simplicidad de la comunicacin visual. La ausencia de elementos decorativos en el fondo refuerza el propsito didctico de la imagen. El ambiente general es neutro, propicio para la reflexin sobre el tema del apoyo y la inclusin, sin distracciones visuales que desven la atencin del mensaje principal.', style="width: 1043px").m-auto
            figure.movil
              img(src='@/assets/curso/temas/tema1/figura-2-2v.svg', alt='Descripcin: La ilustracin presenta a una persona pensativa rodeada de burbujas de dilogo con preguntas relacionadas con las acciones de personas con discapacidad. La escena se compone de una figura central, una persona con cabello rojizo y vestimenta verde, situada en el centro de la imagen. Alrededor de ella, se dispersan tres burbujas de dilogo que contienen preguntas relacionadas con la manera en cmo se hace referencia a las personas con discapacidad. La composicin est equilibrada, guiando la vista del espectador a travs de las preguntas y hacia la persona central, quien parece estar reflexionando sobre ellas, sealada por los signos de interrogacin sobre su cabeza. La disposicin de las burbujas de dilogo y su tamao varan, creando un ritmo visual y enfatizando la importancia de cada cuestin. El sujeto principal es una persona joven, con cabello rojizo largo y una expresin de preocupacin, con las manos entrelazadas bajo la barbilla. La vestimenta de la persona consiste en una blusa verde oscura. Los signos de interrogacin sobre su cabeza indican incertidumbre o reflexin. El estilo de la imagen es simple y directo, con lneas limpias y colores planos, lo que sugiere una ilustracin didctica o explicativa. La tcnica empleada parece ser digital, con contornos definidos y un diseo grfico. El uso de burbujas de dilogo es caracterstico de las ilustraciones que buscan comunicar informacin de manera clara y concisa. La paleta de colores es limitada, con tonos suaves y agradables, lo que contribuye a la legibilidad y facilita la comprensin de la informacin. El fondo es blanco, lo que centra la atencin en la persona y las preguntas. La iluminacin es uniforme, sin sombras ni efectos especiales, lo que enfatiza la claridad y la simplicidad de la comunicacin visual. La ausencia de elementos decorativos en el fondo refuerza el propsito didctico de la imagen. El ambiente general es neutro, propicio para la reflexin sobre el tema del apoyo y la inclusin, sin distracciones visuales que desven la atencin del mensaje principal.', style="width: 1043px").m-auto

    .row.justify-content-center.mb-4(data-aos="fade-right")
      .col-lg-10.col-12
        p De acuerdo con lo establecido por el Consorcio Coopi-Care (2015), en el mapeo comunitario se combina el uso de instrumentos grficos y geogrficos, adems de herramientas de desarrollo comunitario que permiten a los agentes comunitarios analizar su entorno, visualizar los cambios, proponer alternativas de solucin a los problemas encontrados y elaborar planes para mejorar sus condiciones de vida.
        .cajon.cajon.color-secundario-medio.p-4
          p Entre los usos del mapeo comunitario se pueden mencionar, la elaboracin de diagnsticos, planificacin de actividades, monitoreo y evaluacin e intercambio de informacin; puntos importantes que permiten contribuir en el desarrollo de las comunidades.
          p.mb-0 Lo siguientes son los pasos para la elaboracin del mapeo comunitario: 

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 Paso 1. Definicin del propsito

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-5.col-10.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src='@/assets/curso/temas/tema1/img-69.png', alt='Imagen decorativa')
      .col-lg-7       
        .row.justify-content-center.mb-3
          .col-lg-2.d-none.d-lg-block(data-aos="zoom-in")
            figure
              img(src='@/assets/curso/temas/tema1/img-70.svg', alt='Imagen decorativa')
          .col-lg-10
            p.mb-0(data-aos="fade-left") El mapeo comunitario es una herramienta verstil y puede servir para muchos propsitos diferentes.
        p El primer paso para la realizacin del mapeo comunitario es la definicin del propsito. Para ello, se deben contestar las siguientes preguntas: 
        ul.lista-ul--color
          li.d-flex.align-items-start
            i.fas.fa-user
            | Cul es el propsito de llevar a cabo el ejercicio de mapeo comunitario?  
          li.d-flex.align-items-start
            i.fas.fa-user
            | Qu tipos de datos son necesarios para alcanzar este objetivo?
          li.d-flex.align-items-start
            i.fas.fa-user
            | Cmo se utilizar la informacin recopilada?

    p.mb-4 La siguiente tabla, explica los aspectos a tener en cuenta en este primer paso:

