        p.mb-0 En salud pblica y procesos comunitarios, la matriz permite que lderes, agentes y habitantes dialoguen abiertamente sobre necesidades reales, prioricen las ms relevantes y acuerden de manera colaborativa las soluciones viables. Favorece la transparencia, el empoderamiento y el seguimiento de los resultados fortaleciendo la corresponsabilidad en la gestin local.
