            h3.mb-0 C. Grupos focales
    
    p El valor del grupo focal en el diagnstico participativo radica en su capacidad para ir ms all de la mera recoleccin de datos, facilitando una verdadera inmersin en la cosmovisin y la lgica comunitaria. Un moderador hbil puede guiar la conversacin para identificar las prioridades sentidas por la comunidad, entender sus prcticas tradicionales y captar las narrativas locales sobre salud y bienestar. Esta tcnica no solo genera informacin cualitativa rica y contextualizada, sino que tambin empodera a los participantes al validar sus voces y experiencias. Al sentirse escuchados y representados, los miembros de la comunidad desarrollan un mayor sentido de apropiacin sobre los resultados del diagnstico, lo que es fundamental para la posterior planificacin y ejecucin de acciones participativas.
    p.mb-4 Lo anterior, se representa de la siguiente manera:

