    Separador

    #t_1_8.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.8. Anlisis participativo comunitario

    p.mb-4 El anlisis participativo es un mtodo que pone en prctica los mtodos y las herramientas mencionadas previamente. En l se involucra a las comunidades en la investigacin y comprensin de sus propios desafos, necesidades y recursos. A diferencia de un anlisis convencional, donde un experto externo recopila y evala los datos, en este enfoque la comunidad es la principal investigadora. Los miembros de la comunidad, junto con facilitadores externos, analizan su propia situacin, identifican las causas de sus problemas y definen las posibles soluciones desde su perspectiva (Gom, 2007).

    .row.no-gutters.align-items-center.mb-4.style___box_1.m-0(data-aos="zoom-in")
      .col-xl-auto.px-0
        figure.d-none.d-xl-block
          img.img-hero(src='@/assets/curso/temas/tema1/img-75.png', alt='').m-auto
      .col-xl.px-0
        .d-flex.align-items-start
          img.icon-inline.d-none.d-lg-block.me-3(src='@/assets/curso/temas/tema1/img-76.svg' alt="Imagen decorativa")
        p.mb-0 Este proceso busca empoderar a las personas al validar sus conocimientos y experiencias. Al participar activamente, la comunidad no solo genera informacin ms rica y contextualizada, sino que tambin fortalece su capacidad para la toma de decisiones y la accin colectiva. El anlisis participativo es, en esencia, un proceso de aprendizaje mutuo y empoderamiento que promueve la autogestin y la sostenibilidad de los proyectos, ya que las soluciones propuestas son diseadas y apropiadas por quienes se vern directamente beneficiados por ellas. 

    p.mb-4 El proceso se lleva a cabo por medio de las siguientes fases en dicho orden: 

    .bg-c2.px-md-5.px-4.mb-0
      .tarjeta.BGIMG01.mb-4
        .p-4.p-lg-5
          SlyderA(tipo="b")
            .row.justify-content-center
              .col-lg-7.order-lg-1.order-2
                h4 Preparacin y el acercamiento a la comunidad 
                p En esta fase, los facilitadores establecen un primer contacto, se presentan y explican el propsito de la metodologa, buscando generar confianza y legitimidad. Se identifican los lderes, las organizaciones y los actores clave de la comunidad. El objetivo no es imponer una agenda, sino escuchar las preocupaciones y los temas que la comunidad considera relevantes. Es un momento crucial para negociar la participacin y asegurar que el proceso sea mutuamente beneficioso, respetando la cosmogona y la dinmica social existentes. El acercamiento con la comunidad puede ser mediante las tcnicas previamente mencionadas en este componente (el mapeo comunitario, grupos focales, encuestas participativas, entre otros), con el fin de identificar e involucrar a los lderes en la comunidad que sern los representantes de la comunidad en los espacios de discusin. 
              .col-lg-5.col-12.order-lg-2.order-1.mb-lg-0.mb-4
                figure
                  img(src='@/assets/curso/temas/tema1/img-77.png' style="width: 500px" ,alt='').m-auto
            .row.justify-content-center
              .col-lg-7.order-lg-1.order-2
                h4 Diagnstico y mapeo de la realidad 
                p Una vez que se establece la confianza, se pasa a esta fase. Aqu, se utilizan una variedad de herramientas visuales y creativas para que la comunidad explore su propio contexto. Mtodos como el mapeo comunitario permiten a los participantes dibujar y sealar los recursos, problemas y riesgos en un mapa de su territorio. Asimismo, las matrices de problemas y soluciones ayudan a identificar las causas subyacentes de las problemticas y a priorizar las reas de intervencin. En esta etapa, el facilitador gua, pero la comunidad es quien dirige el anlisis y la reflexin. 
              .col-lg-5.col-12.order-lg-2.order-1.mb-lg-0.mb-4
                figure
                  img(src='@/assets/curso/temas/tema1/img-78.png' style="width: 500px" ,alt='').m-auto
            .row.justify-content-center
              .col-lg-7.order-lg-1.order-2
                h4 Anlisis y priorizacin
                p La informacin recopilada en el diagnstico se analiza de manera colectiva. Los participantes discuten y debaten sobre las relaciones de causa y efecto, las dinmicas de poder y los factores que influyen en sus situaciones. Este anlisis profundo y compartido permite a la comunidad ir ms all de los sntomas para abordar las races de los problemas. Debe hacerse de manera integral, profundizando en el porqu del comportamiento de los reportes y no nicamente en si estos se confirmaron o descartaron, adems, en el uso de datos generados desde el comportamiento epidemiolgico territorial con el fin de generar acciones individuales y colectivas, activando la autogestin y movilizacin social. (Gom, 2007). Por esto, no se debe desconocer las formas de organizacin existentes en la comunidad, siendo estas las posibles redes a consolidar. 
              .col-lg-5.col-12.order-lg-2.order-1.mb-lg-0.mb-4
                figure
                  img(src='@/assets/curso/temas/tema1/img-79.png' style="width: 500px" ,alt='').m-auto
            .row.justify-content-center
              .col-lg-7.order-lg-1.order-2
                h4 Planificacin de la accin y la movilizacin
                p Con los problemas priorizados, la comunidad, con el apoyo de los facilitadores, disea un plan de accin concreto. Se definen objetivos claros, se asignan roles y responsabilidades; adems, se establecen los plazos. Este plan no es un documento esttico, sino un compromiso vivo y flexible. En esta etapa, el anlisis se convierte en el motor para la movilizacin social, donde la comunidad se organiza para implementar las soluciones que ha cocreado, ya sea a travs de proyectos de autogestin, campaas de incidencia o la colaboracin con otras instituciones.
              .col-lg-5.col-12.order-lg-2.order-1.mb-lg-0.mb-4
                figure
                  img(src='@/assets/curso/temas/tema1/img-79.png' style="width: 500px" ,alt='').m-auto
            .row.justify-content-center
              .col-lg-7.order-lg-1.order-2
                h4 Evaluacin y la reflexin
                p Una vez que las acciones estn en marcha, la comunidad evala los resultados de forma participativa. Se discute qu funcion, qu no y por qu. Este proceso de retroalimentacin es vital para ajustar las estrategias y asegurar la sostenibilidad del proyecto a largo plazo. La evaluacin no solo mide el xito, sino que tambin fortalece la capacidad de la comunidad para aprender de sus propias experiencias, convirtindose en un agente de cambio continuo y autnomo.  
              .col-lg-5.col-12.order-lg-2.order-1.mb-lg-0.mb-4
                figure
                  img(src='@/assets/curso/temas/tema1/img-79.png' style="width: 500px" ,alt='').m-auto

      .row.justify-content-center.mb-4(data-aos="flip-up")
        .col-lg-12.col-12
          p El anlisis participativo es una herramienta fundamental en el proceso de la Vigilancia Basada en Comunidad. Este proceso es realizado por los miembros de la comunidad, agentes comunitarios y personal de salud e instituciones que son parte de un territorio, con el fin de cooperar con la identificacin, anlisis y movilizacin social, frente a las situaciones de inters en salud pblica que se presentan en estos (Geilfus, 2002). Para esto, la comunidad se vale de las Redes de Vigilancia Epidemiolgica Basada en Comunidad (REVCOM), siendo esta la encargada de emitir las seales al Sistema de Alerta Temprana (SAT), para iniciar la gestin del riesgo colectivo e individual de estas. Con la participacin activa de la comunidad se favorece la proteccin de la salud, as como, influir en la toma de decisiones y ejercer control social de las acciones que se desarrollan en los territorios. Estas acciones pueden realizarse en espacios como COVECOM, comits locales, mesas de trabajo, en los que se comparte la presentacin de las situaciones de salud, las actividades realizadas con la comunidad, as como la respuesta institucional a estas. 












